
import Datetime from 'vux/src/components/datetime/index.vue'

import { mapGetters } from 'vuex'
import cookie from '../../../static/js/cookie'
export default {
name: 'Editbirthday',
components: {
Datetime
},
computed: {
...mapGetters({
setInfo: 'userInfo'
})
},
created () {
this.getUserInfo()
},
data () {
return {
avatar: sessionStorage.getItem('avatar'),
realname: '',
year: '2010-01-01',
seconds: '请选择出生时辰',
sex: '',
type: '公历',
}
},
methods: {
change (value) {
this.year = value
},
changeSex (value) {
this.sex = value
},
changeType (value) {
this.type = value
},

getUserInfo: function () {
let id = cookie.getCookie('mid');
return this.$api.userInfo(id).then(res => {
this.realname = res.data.realname
this.type = res.data.type?res.data.type:'公历'
this.year = res.data.year?res.data.year:'2010-01-01'
this.sex = res.data.sex
this.seconds = res.data.seconds?res.data.seconds:'请选择出生时辰'
}).catch(err => {
this.$vux.toast.text('请登录')
return err
})
},
query () {
let data = {
"type": this.type,
"year": this.year,
"realname": this.realname,
"sex": this.sex,
"seconds": this.seconds,
}
return this.$api.updateUserInfo(cookie.getCookie('mid'), data).then(res => {
this.$vux.toast.text('成功')
sessionStorage.setItem('nickname', res.data.nickname)
setTimeout(() => {
this.$router.back()
}, 2000)
}).catch(err => {
this.$vux.toast.text('错误')
return err
})
}
}
}



